import React from 'react';
import './PartnersThanks.scss';

const PartnersThanks = () => (
	<div className="partner-form-wrapper partner-form-wrapper--thanks">
		<div className="form-wrapper">
			<div className="partner-thanks-header">
				<div className="partner-form-logo-wrapper">
					<img
						className=" partner-form-logo"
						src="data:image/svg+xml;base64," // eslint-disable-line
						alt="partner-logo"
					/>
				</div>
			</div>

			<div className="partner-thanks-text">
				<h4 className="gracias">¡Gracias!</h4>
				<p><strong>Tus datos fueron enviados correctamente.</strong>
					<br /> En 30 minutos podrás empezar a disfrutar del beneficio.
				</p>
			</div>
		</div>
		<a
			className="partners-thanks-send"
			href="https://www.walmart.com.ar/"
		>Ir a Walmart.com.ar
		</a>
	</div>
);

export default PartnersThanks;
